

.content{
background-color: rgb(255, 255, 255);        
width: 800px;
height: 160px;

display: flex;
justify-content: left;
margin-left: 20px;

}

.foto{
    width: 40px;
    box-shadow: 10px 10px 10px rgb(128, 127, 127);
    border-radius: 20px;
    cursor: pointer;
    border: 0.5px solid black;
}
.foto:hover{
transform: scale(1.2);
transition: 1s;
}

.ram{
    padding: 5px;
}